<template>
  <div style="margin-top: 40px">
    <div class="tests-area">
      <h1> 当前运行 </h1>
      <el-card style="text-align: left;width:98%">
        <div v-for="test in tests1" :key="test.id">
          <div style="float:left;width:85%;height: 50px;">
            <router-link class="test-link" :to="{path:'/test/testDetails',query:{id: test.id}}"><span style="font-size: 20px"><strong> ID:{{test.id}} </strong></span></router-link>
            <span> Target:{{test.system.type}} </span>
            <span> {{test.date}} </span>
            </div>
        </div>
      </el-card>

      <h1> 等待运行 </h1>
      <div>
      <el-row>
        <el-col
          v-for="test in tests0"
          :key="test.id"
          :span="8"
        >
          <el-card style="text-align: left;width:94%;height:70%">
            <div style="float:left;width:85%;height: 100px;">
              <div><span style="font-size: 20px"><strong> ID:{{test.id}} </strong></span></div>
             <div><span> Target:{{test.system.type}} </span></div>
             <div><span> {{test.date}} </span></div>
           </div>
         </el-card>
        </el-col>
      </el-row>
      </div>

      <h1> 运行结束 </h1>
      <div>
      <el-row>
        <el-col
          v-for="test in tests2"
          :key="test.id"
          :span="8"
       >
        <el-card style="text-align: left;width:94%;height:70%">
          <div style="float:left;width:85%;height: 100px;">
             <div><span style="font-size: 20px"><strong> ID:{{test.id}} </strong></span></div>
             <div><span> Target:{{test.system.type}} </span></div>
             <div><span> {{test.date}} </span></div>
            </div>
          </el-card>
        </el-col>
      </el-row>
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    name: 'Tests',
    data () {
      return {
        tests1: [],
        tests0: [],
        tests2: []
      }
    },
    mounted () {
      console.log('Enter Tests.vue')
      this.loadTestcases1()
      this.loadTestcases0()
      this.loadTestcases2()
    },
    methods: {
      loadTestcases1 () {
        var _this = this
        this.$axios.get('/testcases/state' + '/1').then(resp => {
          if (resp && resp.data.code === 200) {
            console.log('f:loadTestcases1 get succeed')
            _this.tests1 = resp.data.result
          }
        })
      },
      loadTestcases0 () {
        var _this = this
        this.$axios.get('/testcases/state' + '/0').then(resp => {
          if (resp && resp.data.code === 200) {
            _this.tests0 = resp.data.result
          }
        })
      },
      loadTestcases2 () {
        var _this = this
        this.$axios.get('/testcases/state' + '/2').then(resp => {
          if (resp && resp.data.code === 200) {
            _this.tests2 = resp.data.result
          }
        })
      },
      toTestDetails () {
        this.$router.push('/test/TestDetails')
      }
    }
  }
</script>

<style scoped>
  .tests-area {
    width: 990px;
    height: 750px;
    margin-left: auto;
    margin-right: auto;
  }

  .test-link {
    text-decoration: none;
    color: #606266;
  }

  .test-link:hover {
    color: #409EFF;
  }
</style>



